/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent; }

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0; }
  .slick-list:focus {
    outline: none; }
  .slick-list.dragging {
    cursor: pointer;
    cursor: hand; }

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0); }

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto; }
  .slick-track:before, .slick-track:after {
    content: "";
    display: table; }
  .slick-track:after {
    clear: both; }
  .slick-loading .slick-track {
    visibility: hidden; }

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none; }
  [dir="rtl"] .slick-slide {
    float: right; }
  .slick-slide img {
    display: block; }
  .slick-slide.slick-loading img {
    display: none; }
  .slick-slide.dragging img {
    pointer-events: none; }
  .slick-initialized .slick-slide {
    display: block; }
  .slick-loading .slick-slide {
    visibility: hidden; }
  .slick-vertical .slick-slide {
    display: block;
    height: auto;
    border: 1px solid transparent; }

.slick-arrow.slick-hidden {
  display: none; }

.slick-dots {
  position: absolute;
  bottom: 50px;
  left: calc(50% - 125px);
  list-style: none;
  display: block;
  padding: 0;
  margin: 0; }
  @media screen and (min-width: 1770px) {
    .slick-dots {
      bottom: 50px;
      left: 12%; } }
  .slick-dots li {
    position: relative;
    display: inline-block;
    height: 20px;
    width: 20px;
    margin: 0 8px;
    padding: 0;
    cursor: pointer; }
    .slick-dots li button {
      border: 1px solid #6e6e6e;
      border-radius: 50%;
      background-color: #F50049;
      display: block;
      height: 20px;
      width: 20px;
      outline: none;
      line-height: 0;
      font-size: 0;
      padding: 0;
      cursor: pointer;
      vertical-align: top; }
      .slick-dots li button:hover {
        outline: #00b1ff;
        background-color: #5c001b; }
        .slick-dots li button:hover:before {
          opacity: 1; }
      .slick-dots li button:before {
        position: absolute;
        top: 5px;
        left: 5px;
        content: '';
        display: block;
        border-radius: 50%;
        width: 10px;
        height: 10px;
        background-color: #F50049; }
    .slick-dots li.slick-active button:before {
      background-color: #5c001b;
      opacity: 1; }

.slick-prev,
.slick-next {
  position: absolute;
  display: block;
  height: 20px;
  width: 20px;
  line-height: 0;
  font-size: 0;
  cursor: pointer;
  background: transparent;
  color: transparent;
  top: 50%;
  transform: translate(0, -50%);
  padding: 0;
  border: none;
  outline: none;
  z-index: 10; }
  .slick-prev:hover, .slick-prev:focus,
  .slick-next:hover,
  .slick-next:focus {
    outline: none;
    background: transparent;
    color: transparent; }
    .slick-prev:hover:before, .slick-prev:focus:before,
    .slick-next:hover:before,
    .slick-next:focus:before {
      opacity: 1;
      text-shadow: 0 0 5px #F50049; }
  .slick-prev.slick-disabled:before,
  .slick-next.slick-disabled:before {
    opacity: 0.3; }
  .slick-prev:before,
  .slick-next:before {
    font: 40px/1 'Playfair Display', sans-serif;
    color: #F50049;
    opacity: 0.7; }

.slick-prev {
  left: 50px; }
  @media screen and (max-width: 1280px) {
    .slick-prev {
      left: 15px; } }
  .slick-prev:before {
    content: '<'; }

.slick-next {
  right: 50px; }
  @media screen and (max-width: 1280px) {
    .slick-next {
      right: 15px; } }
  .slick-next:before {
    content: '>'; }

